import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue';
import { getTodayTom } from '../../utils/dataChange.js';
export default {
	data() {
		return {
			current: 0,
			days: []
		};
	},
	components: {
		uniSegmentedControl
	},
	onShow() {
		let date = new Date();
		let today = getTodayTom(0, '-');
		today = '今天' + today;
		this.days.push(today);
		console.log('today' + JSON.stringify(today));
		let tomorrow = getTodayTom(1, '-');
		tomorrow = '明天' + tomorrow;
		this.days.push(tomorrow);
		let houtian = getTodayTom(2, '-');
		houtian = '后天' + houtian;
		this.days.push(houtian);
	},
	methods: {
		onClickItem(e) {
			console.log('e' + JSON.stringify(e));
		},
		goMovieSession() {
			uni.navigateTo({
				url:'./movieSession'
			})
		}
	}
};
